@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap");

body {
  background-color: #f5f4ef;
}

#root {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.container {
  width: 100%;
  display: grid;
  grid-column: 1/-1;
  flex-direction: column;
  align-items: center;
  position: relative;
}
h3 {
  text-overflow: ellipsis;
}

.container-search {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  max-width: 643px;
  margin: 0 auto;
  flex-direction: row;
}

.container-search .container-input {
  border-radius: 20px 0 0 20px;
  height: 101px;
  background: #6e5773;
  border: none;
  width: 600px;
}

.container-search input {
  font-size: 2rem;
  padding: 2rem 2.2rem;
  height: 101px;
  border: none;
  font-family: "Inter", sans-serif;
  font-weight: 800;
  color: white;
  background: #6e5773;
  width: inherit;
  border-radius: 20px 0 0 20px;
}

.container-search input:focus-visible {
  outline: 0px;
  
}



input[type="search"]:focus {
  border: none;
}

.container-search input::placeholder {
  color: rgba(255, 255, 255, 0.4);
  font-size: 1.5rem;
  padding: 0 1rem;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}

.container-search button {
  width: 110px;
  height: 101px;
  background: #d45d78;
  border-radius: 0 20px 20px 0;
  color: white;
  border: none;
  cursor: pointer;
}

.container-search button img {
  color: white;
}

.container-search .title-container {
  width: 100%;
  height: 80px;
  border-radius: 0 0 20px 20px;
  background: #d45d78;
}

.container-result {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  padding: 2rem;
  margin: 0 auto;
  position: relative;
  gap: 60px;
}

.search-item {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  object-fit: cover;
  width: 346px;
  height: 495px;
  position: relative;
  background: #6e5773;
  border-radius: 20px;
  text-decoration: none;
  overflow: hidden;
  text-overflow: ellipsis;
  transform: scale(1);
  animation-name: scaleOff;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.search-item:hover {
  transform: scale(1.1);
  animation-name: scaleOn;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.search-item img {
  width: 100%;
  object-fit: cover;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
}

.search-item .title-container {
  width: 100%;
  height: 80px;
  color: white;
  text-decoration: none;
  background: #d45d78;
  border-radius: 0 0 20px 20px;
}

.search-popup {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  /* background-color: black; */
}

.image-container {
  height: 100vh;
  width: 100%;
}

