/* animation */

@keyframes goTop {
  0% {
    height: 100vh;
  }
  100% {
    height: 20vh;
  }
}

@keyframes goBack {
  0% {
    height: 20vh;
  }
  100% {
    height: 100vh;
  }
}

.animation-forward {
  animation-name: goTop;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.animation-backward {
  animation-name: goBack;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.animation-showContent {
  animation-name: showContent;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

.animation-hideContent {
  animation-name: hideContent;
  animation-duration: 4s;
  animation-fill-mode: forwards;
}

@keyframes scaleOn {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}

@keyframes scaleOff {
  0% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes showContent {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes hideContent {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
